import React, { useState, useEffect } from 'react';
import { useSearchParams } from 'react-router-dom';
import Page from '@/components/Page';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Badge } from '@/components/ui/badge';
import {
  Search,
  Webhook,
  Loader2,
  Plus,
  Trash2,
  Eye,
  Calendar,
  Activity,
  MoreHorizontal,
  Filter,
  Download,
  Settings
} from 'lucide-react';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table';
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from '@/components/ui/dropdown-menu';
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogFooter,
} from '@/components/ui/dialog';
import { toast } from 'sonner';
import axiosInstance from '@/api/axios/axiosInstance';
import WebhookCreator from './WebhookCreator';
import * as facebookService from '@/api/services/facebook/facebookService';
import { useAuth } from '@/context/AuthContext';

type ViewType = 'list' | 'create' | 'view';

const Webhooks: React.FC = () => {
  const [searchParams, setSearchParams] = useSearchParams();
  const { user } = useAuth();
  const isSuperAdmin = user?.isSuperAdmin || false;
  const [currentView, setCurrentView] = useState<ViewType>('list');
  const [selectedWebhook, setSelectedWebhook] = useState<any>(null);
  const [searchQuery, setSearchQuery] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [existingWebhooks, setExistingWebhooks] = useState<any[]>([]);

  // Workflow naming modal states
  const [showNameModal, setShowNameModal] = useState(false);
  const [workflowName, setWorkflowName] = useState('');
  const [isCreatingWorkflow, setIsCreatingWorkflow] = useState(false);

  // Enhanced URL state management for webhook creator and Gmail OAuth redirect
  useEffect(() => {
    const view = searchParams.get('view');
    const workflow = searchParams.get('workflow');
    const tempId = searchParams.get('tempId');
    const workflowId = searchParams.get('workflowId');
    const gmailSuccess = searchParams.get('gmail_success');
    const gmailError = searchParams.get('gmail_error');
    const email = searchParams.get('email');
    const cardId = searchParams.get('card_id');

    console.log('üîç URL State Check:', { view, workflow, tempId, workflowId, gmailSuccess, gmailError, email, cardId });

    // Check if we should show webhook creator
    if (view === 'creator') {
      if (workflow) {
        setWorkflowName(decodeURIComponent(workflow));
      }

      // Restore workflow state from localStorage if available
      const savedState = localStorage.getItem('current_workflow_state');
      if (savedState && tempId) {
        try {
          const state = JSON.parse(savedState);
          if (state.tempId === tempId) {
            setWorkflowName(state.name);
            
            // If we have a webhookId, load the existing webhook
            if (state.webhookId) {
              // Set the id in the URL to load the existing webhook
              const newParams = new URLSearchParams(searchParams);
              newParams.set('id', state.webhookId.toString());
              setSearchParams(newParams);
            }
            
            console.log('üîÑ Restored workflow state:', state);
          }
        } catch (error) {
          console.error('Error restoring workflow state:', error);
        }
      }

      setCurrentView('create');

      // Handle Gmail OAuth redirect
      if (gmailSuccess || gmailError) {
        // Keep the tempId and card_id parameters for workflow restoration
        const newParams = new URLSearchParams(searchParams);
        newParams.delete('gmail_success');
        newParams.delete('gmail_error');
        newParams.delete('email');
        setSearchParams(newParams);
      }
    // Handle editing existing workflow
    else if (workflowId) {
      const existingWorkflow = existingWebhooks.find(w => w.id === parseInt(workflowId));
      if (existingWorkflow) {
        setSelectedWebhook(existingWorkflow);
        setCurrentView('view');
      }
    }
    }
  }, [searchParams, setSearchParams, existingWebhooks]);

  // Handle Facebook OAuth callback (redirect to frontend with code/state)
  useEffect(() => {
    const code = searchParams.get('code');
    const state = searchParams.get('state');
    if (code) {
      (async () => {
        try {
          await facebookService.connectFacebookAccount(code, state || undefined);
          // Clean OAuth params but preserve current workflow/search state
          const newParams = new URLSearchParams(searchParams);
          newParams.delete('code');
          newParams.delete('state');
          setSearchParams(newParams);
          // Optionally refresh any Facebook-related data here if needed
        } catch (e) {
          // Error toast is handled inside the service
          const newParams = new URLSearchParams(searchParams);
          newParams.delete('code');
          newParams.delete('state');
          setSearchParams(newParams);
        }
      })();
    }
  }, [searchParams, setSearchParams]);

  // Rehydrate creator state on initial load if URL lacks params but localStorage has state
  useEffect(() => {
    const hasView = !!searchParams.get('view');
    if (hasView) return;

    const tryRestoreFrom = (key: string) => {
      const saved = localStorage.getItem(key);
      if (!saved) return false;
      try {
        const state = JSON.parse(saved);
        if (!state || !state.tempId) return false;
        const newParams = new URLSearchParams(searchParams);
        newParams.set('view', 'creator');
        if (state.name) newParams.set('workflow', encodeURIComponent(state.name));
        newParams.set('tempId', state.tempId);
        setSearchParams(newParams);
        return true;
      } catch {
        return false;
      }
    };

    // Prefer the lightweight current_workflow_state, then fall back to detailed creator state
    if (tryRestoreFrom('current_workflow_state')) return;
    tryRestoreFrom('webhook_creator_state');
  }, []);

  // Load existing webhooks
  const loadExistingWebhooks = async (showLoading = false) => {
    try {
      if (showLoading) {
        setIsLoading(true);
      }
      const response = await axiosInstance.get('/api/webhooks');
      if (response.data.success) {
        console.log('Loaded webhooks:', response.data.data);
        if (response.data.data.length > 0) {
          console.log('Sample webhook structure:', response.data.data[0]);
        }
        setExistingWebhooks(response.data.data);
      }
    } catch (error) {
      console.error('Error loading webhooks:', error);
    } finally {
      if (showLoading) {
        setIsLoading(false);
      }
    }
  };

  // Delete webhook
  const deleteWebhook = async (webhookId: string) => {
    try {
      setIsLoading(true);
      console.log('Attempting to delete webhook with ID:', webhookId);
      const response = await axiosInstance.delete(`/api/webhooks/${webhookId}`);
      if (response.data.success) {
        toast.success('Webhook deleted successfully!');
        loadExistingWebhooks(true); // Refresh the list with loading
      } else {
        throw new Error(response.data.message || 'Failed to delete webhook');
      }
    } catch (error: any) {
      console.error('Error deleting webhook:', error);
      console.error('Full error response:', error.response);
      toast.error(error.response?.data?.message || 'Failed to delete webhook');
      setIsLoading(false);
    }
  };

  // View webhook details
  const viewWebhook = (webhook: any) => {
    console.log('Viewing webhook:', webhook);
    setSelectedWebhook(webhook);
    setCurrentView('view');
    // Persist view state in URL for refresh resilience
    const newParams = new URLSearchParams(searchParams);
    newParams.set('workflowId', String(webhook.id));
    newParams.set('view', 'view');
    if (webhook.name) {
      newParams.set('workflow', encodeURIComponent(webhook.name));
    }
    setSearchParams(newParams);
  };

  // Handle create workflow button click - show naming modal first
  const handleCreateWorkflow = () => {
    setWorkflowName('');
    setShowNameModal(true);
  };

  // Handle workflow name save and navigate to creator
  const handleSaveWorkflowName = async () => {
    if (!workflowName.trim()) {
      toast.error('Please enter a workflow name');
      return;
    }

    setIsCreatingWorkflow(true);
    try {
      console.log('üöÄ Creating new workflow with name:', workflowName);

      // Generate a temporary workflow ID for state management
      const tempWorkflowId = `temp_${Date.now()}`;

      // Close modal and navigate to creator with the workflow name
      setShowNameModal(false);
      setCurrentView('create');

      // Set URL state to maintain creator view on refresh with workflow ID
      setSearchParams({
        view: 'creator',
        workflow: encodeURIComponent(workflowName),
        tempId: tempWorkflowId
      });

      // Store workflow state in localStorage for persistence
      localStorage.setItem('current_workflow_state', JSON.stringify({
        name: workflowName,
        tempId: tempWorkflowId,
        created: Date.now(),
        status: 'creating'
      }));

      toast.success(`Workflow "${workflowName}" ready for configuration!`);
    } catch (error) {
      console.error('Error creating workflow:', error);
      toast.error('Failed to create workflow');
    } finally {
      setIsCreatingWorkflow(false);
    }
  };

  // Handle Gmail OAuth callback parameters
  useEffect(() => {
    const gmailSuccess = searchParams.get('gmail_success');
    const gmailError = searchParams.get('gmail_error');
    const email = searchParams.get('email');

    if (gmailSuccess === 'true' && email) {
      toast.success(`Gmail account connected successfully! (${decodeURIComponent(email)})`);
      // Preserve current workflow/search state; clear only Gmail-related params
      const newParams = new URLSearchParams(searchParams);
      newParams.delete('gmail_success');
      newParams.delete('gmail_error');
      newParams.delete('email');
      setSearchParams(newParams);
    } else if (gmailError) {
      const errorMessage = decodeURIComponent(gmailError);
      toast.error(`Gmail connection failed: ${errorMessage}`);
      // Preserve current workflow/search state; clear only Gmail-related params
      const newParams = new URLSearchParams(searchParams);
      newParams.delete('gmail_success');
      newParams.delete('gmail_error');
      newParams.delete('email');
      setSearchParams(newParams);
    }
  }, [searchParams, setSearchParams]);

  // Load webhooks on component mount
  useEffect(() => {
    loadExistingWebhooks(true); // Show loading on initial load
  }, []);

  // Filter webhooks based on search
  const filteredWebhooks = existingWebhooks.filter(webhook =>
    webhook.name?.toLowerCase().includes(searchQuery.toLowerCase()) ||
    webhook.trigger_type?.toLowerCase().includes(searchQuery.toLowerCase())
  );

  // Handle back navigation with loading
  const handleBackToList = () => {
    setCurrentView('list');
    // Clear URL state and localStorage when going back to list
    setSearchParams({});
    localStorage.removeItem('current_workflow_state');
    localStorage.removeItem('webhook_creator_state');
    loadExistingWebhooks(true); // Show loading when navigating back
  };

  // Render different views based on current state
  if (currentView === 'create') {
    return (
      <WebhookCreator
        workflowName={workflowName}
        onBack={handleBackToList}
        onSave={() => {
          setCurrentView('list');
          loadExistingWebhooks(true); // Show loading after saving
        }}
      />
    );
  }

  if (currentView === 'view' && selectedWebhook) {
    return (
      <WebhookCreator
        webhook={selectedWebhook}
        onBack={handleBackToList}
        onSave={() => {
          setCurrentView('list');
          loadExistingWebhooks(true); // Show loading after saving
        }}
      />
    );
  }





  return (
    <Page>
      <div className="min-h-screen bg-gray-50">
        <div className="max-w-6xl mx-auto px-4 py-8">
          {/* Header */}
          <div className="flex items-center justify-between mb-8">
            <div>
              <div className="flex items-center gap-4 mb-2">
                <div className="inline-flex items-center justify-center w-12 h-12 bg-blue-600 rounded-xl">
                  <Webhook className="h-6 w-6 text-white" />
                </div>
                <h1 className="text-3xl font-bold text-gray-900">
                  Webhook Workflows
                </h1>
              </div>
              <p className="text-gray-600 ml-16">
                Manage your webhook automations and integrations
              </p>
            </div>
            <Button
              onClick={handleCreateWorkflow}
              className="h-12 px-6 bg-blue-600 hover:bg-blue-700 text-white rounded-lg font-medium"
            >
              <Plus className="h-5 w-5 mr-2" />
              Create Workflow
            </Button>
          </div>

          {/* Controls Bar */}
          <div className="bg-white rounded-lg border border-gray-200 p-4 mb-6">
            <div className="flex items-center justify-between">
              <div className="flex items-center gap-4">
                <div className="relative">
                  <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
                  <Input
                    placeholder="Search webhook workflows..."
                    value={searchQuery}
                    onChange={(e) => setSearchQuery(e.target.value)}
                    className="pl-10 h-10 w-80 border-gray-300 rounded-lg focus:border-blue-500 focus:ring-1 focus:ring-blue-500"
                  />
                </div>
              </div>
              <div className="flex items-center gap-2">
                <Button variant="outline" size="sm" className="h-10 px-3 border-gray-300">
                  <Filter className="h-4 w-4 mr-2" />
                  Filter
                </Button>
                <Button variant="outline" size="sm" className="h-10 px-3 border-gray-300">
                  <Download className="h-4 w-4 mr-2" />
                  Export
                </Button>
              </div>
            </div>
          </div>

          {/* Webhook Table */}
          <div className="bg-white rounded-lg border border-gray-200 shadow-sm">
            {isLoading ? (
              <div className="flex items-center justify-center py-12">
                <Loader2 className="h-8 w-8 animate-spin text-blue-600" />
                <span className="ml-3 text-gray-600">Loading webhooks...</span>
              </div>
            ) : filteredWebhooks.length === 0 ? (
              <div className="p-12 text-center">
                <div className="inline-flex items-center justify-center w-16 h-16 bg-gray-100 rounded-xl mb-4">
                  <Webhook className="h-8 w-8 text-gray-400" />
                </div>
                <h3 className="text-xl font-semibold text-gray-900 mb-2">No Webhook Workflows</h3>
                <p className="text-gray-600 mb-6">
                  {searchQuery ? 'No workflows match your search criteria.' : 'Get started by creating your first webhook workflow.'}
                </p>
              </div>
            ) : (
              <Table>
                <TableHeader>
                  <TableRow className="border-b border-gray-200">
                    <TableHead className="h-12 px-6 text-left text-sm font-semibold text-gray-900">
                      Workflow Name
                    </TableHead>
                    <TableHead className="h-12 px-6 text-left text-sm font-semibold text-gray-900">
                      Trigger
                    </TableHead>
                    {isSuperAdmin && (
                      <TableHead className="h-12 px-6 text-left text-sm font-semibold text-gray-900">
                        User
                      </TableHead>
                    )}
                    <TableHead className="h-12 px-6 text-left text-sm font-semibold text-gray-900">
                      Status
                    </TableHead>
                    <TableHead className="h-12 px-6 text-left text-sm font-semibold text-gray-900">
                      Created
                    </TableHead>
                    <TableHead className="h-12 px-6 text-right text-sm font-semibold text-gray-900">
                      Actions
                    </TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {filteredWebhooks.map((webhook) => (
                    <TableRow key={webhook.id} className="border-b border-gray-100 hover:bg-gray-50 transition-colors">
                      <TableCell className="h-16 px-6">
                        <div className="flex items-center gap-3">
                          <div className="flex items-center justify-center w-10 h-10 bg-blue-100 rounded-lg">
                            <Webhook className="h-5 w-5 text-blue-600" />
                          </div>
                          <div>
                            <div className="font-semibold text-gray-900">{webhook.name}</div>
                            <div className="text-sm text-gray-500 mt-1">
                            {/* 67890321` 1q2W#4321 ZQWZsedrftgy ko0
                            "? */}
                            </div>
                          </div>
                        </div>
                      </TableCell>
                      <TableCell className="h-16 px-6">
                        <div className="flex items-center gap-2">
                          <Activity className="h-4 w-4 text-gray-400" />
                          <span className="text-sm text-gray-700">
                            {webhook.trigger_type || 'Unknown trigger'}
                          </span>
                        </div>
                      </TableCell>
                      {isSuperAdmin && (
                        <TableCell className="h-16 px-6">
                          <div className="text-sm text-gray-700">
                            {webhook.username || webhook.email || `User ${webhook.user_id}`}
                            {webhook.first_name && webhook.last_name && (
                              <div className="text-xs text-gray-500 mt-1">
                                {webhook.first_name} {webhook.last_name}
                              </div>
                            )}
                          </div>
                        </TableCell>
                      )}
                      <TableCell className="h-16 px-6">
                        <Badge
                          variant={webhook.is_active ? 'default' : 'secondary'}
                          className="px-3 py-1 text-xs font-medium"
                        >
                          {webhook.is_active ? 'active' : 'inactive'}
                        </Badge>
                      </TableCell>
                      <TableCell className="h-16 px-6">
                        <div className="flex items-center gap-2 text-sm text-gray-600">
                          <Calendar className="h-4 w-4 text-gray-400" />
                          <span>{new Date(webhook.created_at).toLocaleDateString()}</span>
                        </div>
                      </TableCell>
                      <TableCell className="h-16 px-6">
                        <div className="flex items-center justify-end gap-2">
                          <Button
                            variant="ghost"
                            size="sm"
                            onClick={() => viewWebhook(webhook)}
                            className="h-8 px-3 text-blue-600 hover:text-blue-700 hover:bg-blue-50 rounded-md"
                          >
                            <Eye className="h-4 w-4 mr-1" />
                            View
                          </Button>
                          <DropdownMenu>
                            <DropdownMenuTrigger asChild>
                              <Button
                                variant="ghost"
                                size="sm"
                                className="h-8 w-8 p-0 text-gray-400 hover:text-gray-600 hover:bg-gray-100 rounded-md"
                              >
                                <MoreHorizontal className="h-4 w-4" />
                              </Button>
                            </DropdownMenuTrigger>
                            <DropdownMenuContent align="end" className="w-48">
                              <DropdownMenuItem
                                onClick={() => viewWebhook(webhook)}
                                className="flex items-center gap-2 px-3 py-2 text-sm"
                              >
                                <Eye className="h-4 w-4" />
                                View Details
                              </DropdownMenuItem>
                              <DropdownMenuItem
                                onClick={() => {
                                  if (webhook.url) {
                                    navigator.clipboard.writeText(webhook.url);
                                    toast.success('Webhook URL copied to clipboard!');
                                  }
                                }}
                                className="flex items-center gap-2 px-3 py-2 text-sm"
                              >
                                <Settings className="h-4 w-4" />
                                Copy URL
                              </DropdownMenuItem>
                              <DropdownMenuItem
                                onClick={() => deleteWebhook(webhook.webhook_id || webhook.id)}
                                disabled={isLoading}
                                className="flex items-center gap-2 px-3 py-2 text-sm text-red-600 hover:text-red-700 hover:bg-red-50"
                              >
                                {isLoading ? (
                                  <Loader2 className="h-4 w-4 animate-spin" />
                                ) : (
                                  <Trash2 className="h-4 w-4" />
                                )}
                                Delete
                              </DropdownMenuItem>
                            </DropdownMenuContent>
                          </DropdownMenu>
                        </div>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            )}

            {/* Table Footer */}
            {!isLoading && filteredWebhooks.length > 0 && (
              <div className="flex items-center justify-between px-6 py-4 border-t border-gray-200 bg-gray-50">
                <div className="text-sm text-gray-600">
                  Showing {filteredWebhooks.length} of {existingWebhooks.length} webhook{existingWebhooks.length !== 1 ? 's' : ''}
                </div>
                <div className="text-sm text-gray-500">
                  {searchQuery && `Filtered by: "${searchQuery}"`}
                </div>
              </div>
            )}
          </div>
        </div>
      </div>

      {/* Workflow Naming Modal */}
      <Dialog open={showNameModal} onOpenChange={setShowNameModal}>
        <DialogContent className="sm:max-w-md">
          <DialogHeader>
            <DialogTitle className="text-xl font-semibold text-gray-900">
              Create New Workflow
            </DialogTitle>
          </DialogHeader>
          <div className="space-y-4 py-4">
            <div className="space-y-2">
              <label className="text-sm font-medium text-gray-700">
                Workflow Name
              </label>
              <Input
                value={workflowName}
                onChange={(e) => setWorkflowName(e.target.value)}
                placeholder="Enter workflow name..."
                className="w-full h-12 border border-gray-300 rounded-lg focus:border-blue-500 focus:ring-1 focus:ring-blue-500"
                onKeyDown={(e) => {
                  if (e.key === 'Enter') {
                    handleSaveWorkflowName();
                  }
                }}
              />
            </div>
          </div>
          <DialogFooter className="flex gap-3">
            <Button
              variant="outline"
              onClick={() => setShowNameModal(false)}
              className="flex-1 h-12 border border-gray-300 rounded-lg"
            >
              Cancel
            </Button>
            <Button
              onClick={handleSaveWorkflowName}
              disabled={isCreatingWorkflow || !workflowName.trim()}
              className="flex-1 h-12 bg-blue-600 hover:bg-blue-700 text-white rounded-lg disabled:bg-gray-400"
            >
              {isCreatingWorkflow ? (
                <Loader2 className="h-4 w-4 animate-spin mr-2" />
              ) : null}
              Save
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </Page>
  );
};

export default Webhooks;
