import axiosInstance from "@/api/axios/axiosInstance";
import { toast } from "sonner";

// Types
export interface FacebookUser {
  id: string;
  name: string;
  email: string;
}

export interface FacebookConnectionStatus {
  isConnected: boolean;
  facebookUser?: FacebookUser;
  connectedAt?: string;
  expiresAt?: string;
  pages?: FacebookPage[];
  selectedPageId?: string;
  selectedLeadFormId?: string;
  leadForms?: FacebookLeadForm[];
}

export interface FacebookPage {
  id: string;
  name: string;
  category?: string;
  access_token?: string;
}

export interface FacebookLeadForm {
  id: string;
  name: string;
  status: string;
  page_id?: string;
  form_id?: string;
  questions?: Array<{
    key: string;
    label: string;
    type: string;
    options?: string[];
  }>;
}

export interface FacebookLead {
  id: string;
  created_time: string;
  field_data: Array<{
    name: string;
    values: string[];
  }>;
}

// Facebook OAuth URL generation
export async function generateFacebookOAuthUrl(): Promise<string> {
  // Ask backend to generate a signed OAuth URL bound to the logged-in user.
  const res = await axiosInstance.get('/api/auth/facebook/oauth-url');
  if (res.data?.success && res.data.data?.authUrl) return res.data.data.authUrl as string;
  throw new Error(res.data?.message || 'Failed to start Facebook OAuth');
}

// Connect Facebook account
export async function connectFacebookAccount(code: string, state?: string) {
  try {
    // Prefer the backend callback route that already saved credentials
    // This API is kept for compatibility when the frontend receives code directly (e.g., if redirected to frontend).
    const res = await axiosInstance.post("/api/auth/facebook/connect", { code, state });
    
    if (res.data.success) {
      toast.success("Facebook account connected successfully!");
      return res.data.data;
    }
    
    throw new Error(res.data.message || "Failed to connect Facebook account");
  } catch (error: any) {
    console.error("Error connecting Facebook account:", error);
    const message = error.response?.data?.message || "Failed to connect Facebook account";
    toast.error(message);
    throw error;
  }
}

// Get Facebook connection status with timeout (webhook-specific or global)
export async function getFacebookConnectionStatus(webhookIdOrTimeout?: string | number): Promise<FacebookConnectionStatus> {
  try {
    let webhookId: string | undefined;
    let timeout = 30000;

    // Handle overloaded parameters
    if (typeof webhookIdOrTimeout === 'string') {
      webhookId = webhookIdOrTimeout;
    } else if (typeof webhookIdOrTimeout === 'number') {
      timeout = webhookIdOrTimeout;
    }

    // Check webhook-specific connection first
    if (webhookId) {
      console.log('üîç Checking webhook-specific connection for:', webhookId);

      // Check localStorage first (permanent storage)
      const isConnected = localStorage.getItem(`facebook_connected_${webhookId}`) === 'true';
      const userStr = localStorage.getItem(`facebook_user_${webhookId}`);
      console.log('üîç localStorage check:', { isConnected, hasUserStr: !!userStr });

      if (isConnected && userStr) {
        try {
          const facebookUser = JSON.parse(userStr);
          console.log('‚úÖ Found Facebook connection in localStorage');
          return {
            isConnected: true,
            facebookUser,
            connectedAt: new Date().toISOString()
          };
        } catch (e) {
          console.log('‚ùå Invalid localStorage data:', e);
          // Invalid stored data, continue to check session storage
        }
      }

      // Check session storage for temporary connections (during webhook creation)
      const tempConnected = sessionStorage.getItem('temp_facebook_connected') === 'true';
      const tempUserStr = sessionStorage.getItem('temp_facebook_user');
      console.log('üîç sessionStorage check:', { tempConnected, hasTempUserStr: !!tempUserStr });

      if (tempConnected && tempUserStr) {
        try {
          const facebookUser = JSON.parse(tempUserStr);
          console.log('‚úÖ Found Facebook connection in sessionStorage');
          return {
            isConnected: true,
            facebookUser,
            connectedAt: new Date().toISOString()
          };
        } catch (e) {
          console.log('‚ùå Invalid sessionStorage data:', e);
          // Invalid temp data, return not connected
          return { isConnected: false };
        }
      }

      // If both local storage checks fail, check the backend webhook metadata
      console.log('üîç Checking backend webhook metadata for Facebook connection...');
      try {
        const response = await axiosInstance.get(`/api/facebook/connection-status/${webhookId}`, {
          timeout
        });

        if (response.data.success && response.data.data.isConnected) {
          console.log('‚úÖ Found Facebook connection in backend webhook metadata:', response.data.data);

          // Store in localStorage for future quick access
          localStorage.setItem(`facebook_connected_${webhookId}`, 'true');
          if (response.data.data.facebookUser) {
            localStorage.setItem(`facebook_user_${webhookId}`, JSON.stringify(response.data.data.facebookUser));
          }

          return {
            isConnected: true,
            facebookUser: response.data.data.facebookUser,
            pages: response.data.data.pages || [],
            selectedPageId: response.data.data.selectedPageId || '',
            selectedLeadFormId: response.data.data.selectedLeadFormId || '',
            leadForms: response.data.data.leadForms || [],
            connectedAt: new Date().toISOString()
          };
        } else {
          console.log('‚ùå No Facebook connection found in backend webhook metadata');
        }
      } catch (backendError) {
        console.log('‚ùå Error checking backend webhook metadata:', backendError);
      }

      // If all checks fail, return not connected
      console.log('‚ùå No Facebook connection found for webhook');
      return { isConnected: false };
    }

    // If no webhookId provided, return not connected (for new webhook flows)
    console.log('üÜï No webhookId provided - new webhook flow, returning not connected');
    return { isConnected: false };
  } catch (error: any) {
    console.error("Error getting Facebook connection status:", error);
    if (error.name === 'AbortError') {
      toast.error("Connection status check timed out");
    } else {
      toast.error(error.response?.data?.message || "Failed to get connection status");
    }
    return { isConnected: false };
  }
}

// Disconnect Facebook account (global or webhook-specific)
export async function disconnectFacebookAccount(webhookId?: string) {
  try {
    // Clear webhook-specific connection if specified
    if (webhookId) {
      localStorage.removeItem(`facebook_connected_${webhookId}`);
      localStorage.removeItem(`facebook_user_${webhookId}`);
      toast.success("Successfully disconnected Facebook from this webhook");
      return true;
    }

    // Otherwise disconnect globally
    const res = await axiosInstance.delete("/api/auth/facebook/disconnect");

    if (res.data.success) {
      toast.success("Facebook account disconnected successfully!");
      return true;
    }

    throw new Error(res.data.message || "Failed to disconnect Facebook account");
  } catch (error: any) {
    console.error("Error disconnecting Facebook account:", error);
    const message = error.response?.data?.message || "Failed to disconnect Facebook account";
    toast.error(message);
    throw error;
  }
}

