// Subscribe to webhooks
export async function subscribeToWebhooks(pageId: string, webhookUrl: string, verifyToken: string) {
  try {
    const res = await axiosInstance.post("/api/facebook/webhooks/subscribe", {
      pageId,
      webhookUrl,
      verifyToken,
      // Optional context so backend can save mapping to current webhook
      webhookId: localStorage.getItem('current_webhook_id') || undefined,
      formId: (window as any).__selectedFacebookFormId || undefined
    });
    
    if (res.data.success) {
      toast.success("Successfully subscribed to Facebook webhooks!");
      return res.data.data;
    }
    
    throw new Error(res.data.message || "Failed to subscribe to webhooks");
  } catch (error: any) {
    console.error("Error subscribing to webhooks:", error);
    const message = error.response?.data?.message || "Failed to subscribe to webhooks";
    toast.error(message);
    throw error;
  }
}

