import React, { useEffect, useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { toast } from "sonner";
import { Megaphone, Loader2 } from "lucide-react";
import { getAllPhoneNumbers } from "@/api/services/phoneNumbers/phoneNumberService";
import { fetchAllAgentsComplete } from "@/api/services/agents/agentService";
import { getAllWorkflows } from "@/api/services/workflows/workflowService";

type CampaignActionConfigProps = {
  initialConfig?: CampaignActionConfigState;
  onSave: (config: CampaignActionConfigState) => Promise<void> | void;
  onCancel: () => void;
};

export type CampaignActionConfigState = {
  name: string;
  phoneNumberId: string;
  assistantId?: string;
  workflowId?: string;
  autoLaunch?: boolean;
};

export const CampaignActionConfig: React.FC<CampaignActionConfigProps> = ({ initialConfig, onSave, onCancel }) => {
  const [name, setName] = useState(initialConfig?.name || "");
  const [phoneNumberId, setPhoneNumberId] = useState(initialConfig?.phoneNumberId || "");
  const [assistantId, setAssistantId] = useState<string | undefined>(initialConfig?.assistantId);
  const [workflowId, setWorkflowId] = useState<string | undefined>(initialConfig?.workflowId);
  const [autoLaunch, setAutoLaunch] = useState<boolean>(Boolean(initialConfig?.autoLaunch));

  const [phoneNumbers, setPhoneNumbers] = useState<Array<{ id: string; number: string; friendlyName?: string }>>([]);
  const [assistants, setAssistants] = useState<Array<{ id: string; name: string }>>([]);
  const [workflows, setWorkflows] = useState<Array<{ id: string; name: string }>>([]);

  const [loading, setLoading] = useState(false);

  useEffect(() => {
    let mounted = true;
    (async () => {
      try {
        setLoading(true);
        const [phonesRes, assistantsRes, workflowsRes] = await Promise.all([
          // Many list endpoints in this app return { data } at the top level
          getAllPhoneNumbers(undefined, ""),
          fetchAllAgentsComplete("") as any,
          getAllWorkflows(undefined, "", undefined) as any,
        ]);

        if (!mounted) return;

        const phoneList = Array.isArray(phonesRes?.data) ? phonesRes.data : (Array.isArray(phonesRes) ? phonesRes : []);
        setPhoneNumbers(
          (phoneList || []).map((p: any) => ({ id: p.id || p._id || p.phoneNumberId || "", number: p.number || p.phoneNumber || "", friendlyName: p.friendlyName }))
        );

        const assistantList = Array.isArray(assistantsRes?.data) ? assistantsRes.data : (Array.isArray(assistantsRes) ? assistantsRes : []);
        setAssistants((assistantList || []).map((a: any) => ({ id: a.id || a.assistant_id || "", name: a.name || "Unnamed Assistant" })));

        const workflowList = Array.isArray(workflowsRes?.data) ? workflowsRes.data : (Array.isArray(workflowsRes) ? workflowsRes : []);
        setWorkflows((workflowList || []).map((w: any) => ({ id: w.id || w.workflow_id || "", name: w.name || "Untitled Workflow" })));
      } catch (error) {
        console.error("Failed to load campaign deps:", error);
        toast.error("Failed to load campaign dependencies");
      } finally {
        if (mounted) setLoading(false);
      }
    })();
    return () => {
      mounted = false;
    };
  }, []);

  const handleSave = async () => {
    console.log('üîß Campaign handleSave called');
    if (!name.trim()) {
      toast.error("Campaign name is required");
      return;
    }
    if (!phoneNumberId) {
      toast.error("Please select a phone number");
      return;
    }
    const config: CampaignActionConfigState = {
      name: name.trim(),
      phoneNumberId,
      assistantId,
      workflowId,
      autoLaunch,
    };
    console.log('üìã Campaign config to save:', config);
    await onSave(config);
    console.log('‚úÖ Campaign onSave completed');
  };

  return (
    <div className="space-y-6">
      <div className="flex items-center gap-3 pb-4 border-b">
        <div className="w-8 h-8 bg-indigo-600 rounded-lg flex items-center justify-center">
          <Megaphone className="h-5 w-5 text-white" />
        </div>
        <div>
          <h3 className="text-lg font-semibold text-gray-900">Configure Outbound Campaign</h3>
          <p className="text-sm text-gray-500">Set up a campaign to run when this webhook triggers.</p>
        </div>
      </div>

      <div className="space-y-4">
        <div>
          <Label className="text-sm font-medium text-gray-900">Campaign Name</Label>
          <Input value={name} onChange={(e) => setName(e.target.value)} placeholder="e.g. New Leads Outreach" className="mt-1 h-12" />
        </div>

        <div>
          <Label className="text-sm font-medium text-gray-900">Phone Number</Label>
          <Select value={phoneNumberId} onValueChange={setPhoneNumberId}>
            <SelectTrigger className="w-full h-12 mt-1">
              <SelectValue placeholder={loading ? "Loading..." : "Select a phone number"} />
            </SelectTrigger>
            <SelectContent>
              {phoneNumbers.map((p) => (
                <SelectItem key={p.id} value={p.id}>
                  {p.friendlyName ? `${p.friendlyName} (${p.number})` : p.number}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>

        <div>
          <Label className="text-sm font-medium text-gray-900">Assistant (optional)</Label>
          <Select value={assistantId || ""} onValueChange={(v) => setAssistantId(v || undefined)}>
            <SelectTrigger className="w-full h-12 mt-1">
              <SelectValue placeholder={loading ? "Loading..." : "Select an assistant"} />
            </SelectTrigger>
            <SelectContent>
              {assistants.map((a) => (
                <SelectItem key={a.id} value={a.id}>
                  {a.name}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>

        {/* <div>
          <Label className="text-sm font-medium text-gray-900">Workflow (optional)</Label>
          <Select value={workflowId || ""} onValueChange={(v) => setWorkflowId(v || undefined)}>
            <SelectTrigger className="w-full h-12 mt-1">
              <SelectValue placeholder={loading ? "Loading..." : "Select a workflow"} />
            </SelectTrigger>
            <SelectContent>
              {workflows.map((w) => (
                <SelectItem key={w.id} value={w.id}>
                  {w.name}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div> */}

        <div className="flex items-center gap-3 pt-2">
          <input
            id="autoLaunch"
            type="checkbox"
            className="h-4 w-4"
            checked={autoLaunch}
            onChange={(e) => setAutoLaunch(e.target.checked)}
          />
          <Label htmlFor="autoLaunch" className="text-sm text-gray-700">Auto-launch campaign when triggered</Label>
        </div>
      </div>

      <div className="flex gap-3 pt-6 border-t">
        <Button variant="outline" onClick={onCancel} className="flex-1 h-12 border border-gray-300 rounded-lg">
          Cancel
        </Button>
        <Button onClick={handleSave} className="flex-1 h-12 bg-indigo-600 hover:bg-indigo-700 text-white rounded-lg" disabled={loading}>
          {loading ? <><Loader2 className="h-4 w-4 animate-spin mr-2" />Saving...</> : "Save"}
        </Button>
      </div>
    </div>
  );
};

export default CampaignActionConfig;


