import React, { useState, useEffect } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { MessageSquare, Phone, Smartphone, Info, AlertCircle } from 'lucide-react';
import { toast } from 'sonner';

interface TextWebhookConfigProps {
  initialConfig?: TextWebhookConfig;
  onCancel: () => void;
  onSave: (config: TextWebhookConfig) => void;
}

export interface TextWebhookConfig {
  messageTemplate: string;
  fromPhoneNumber: string;
  testPhoneNumber?: string;
  enableNotifications: boolean;
}

const TextWebhookConfig: React.FC<TextWebhookConfigProps> = ({
  initialConfig,
  onCancel,
  onSave
}) => {
  const [config, setConfig] = useState<TextWebhookConfig>({
    messageTemplate: initialConfig?.messageTemplate || 'New lead received from {name} ({email}) at {timestamp}',
    fromPhoneNumber: initialConfig?.fromPhoneNumber || '+13462331126',
    testPhoneNumber: initialConfig?.testPhoneNumber || '',
    enableNotifications: initialConfig?.enableNotifications ?? true
  });

  const [isLoading, setIsLoading] = useState(false);
  const [isTesting, setIsTesting] = useState(false);

  const handleSave = () => {
    if (!config.messageTemplate.trim()) {
      toast.error('Message template is required');
      return;
    }

    if (!config.fromPhoneNumber.trim()) {
      toast.error('From phone number is required');
      return;
    }

    onSave(config);
  };

  const handleTestSMS = async () => {
    if (!config.testPhoneNumber?.trim()) {
      toast.error('Please enter a test phone number');
      return;
    }

    setIsTesting(true);
    try {
      // This would call the backend test endpoint
      const response = await fetch('/api/text-webhook/test', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          toPhoneNumber: config.testPhoneNumber,
          message: config.messageTemplate,
          fromPhoneNumber: config.fromPhoneNumber
        })
      });

      const result = await response.json();
      
      if (result.success) {
        toast.success('Test SMS sent successfully!');
      } else {
        toast.error(`Failed to send test SMS: ${result.error}`);
      }
    } catch (error) {
      console.error('Error testing SMS:', error);
      toast.error('Failed to send test SMS. Please try again.');
    } finally {
      setIsTesting(false);
    }
  };

  const availablePlaceholders = [
    { placeholder: '{name}', description: 'Contact name' },
    { placeholder: '{email}', description: 'Contact email' },
    { placeholder: '{phone}', description: 'Contact phone' },
    { placeholder: '{company}', description: 'Company name' },
    { placeholder: '{message}', description: 'Message content' },
    { placeholder: '{timestamp}', description: 'Current timestamp' },
    { placeholder: '{webhook_id}', description: 'Webhook ID' }
  ];

  return (
    <div className="space-y-6">
      <div className="flex items-center gap-3">
        <div className="flex items-center justify-center w-10 h-10 bg-blue-100 rounded-lg">
          <Smartphone className="h-5 w-5 text-blue-600" />
        </div>
        <div>
          <h3 className="text-lg font-semibold text-gray-900">Text Webhook Configuration</h3>
          <p className="text-sm text-gray-600">Configure SMS notifications for your webhook</p>
        </div>
      </div>

      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <MessageSquare className="h-5 w-5" />
            Message Template
          </CardTitle>
          <CardDescription>
            Customize the SMS message that will be sent. Use placeholders to include dynamic data.
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="space-y-2">
            <Label htmlFor="messageTemplate">Message Template</Label>
            <Textarea
              id="messageTemplate"
              placeholder="Enter your message template..."
              value={config.messageTemplate}
              onChange={(e) => setConfig({ ...config, messageTemplate: e.target.value })}
              rows={4}
              className="font-mono text-sm"
            />
          </div>

          <div className="space-y-2">
            <Label>Available Placeholders</Label>
            <div className="flex flex-wrap gap-2">
              {availablePlaceholders.map((item) => (
                <Badge
                  key={item.placeholder}
                  variant="outline"
                  className="cursor-pointer hover:bg-gray-100"
                  onClick={() => {
                    const textarea = document.getElementById('messageTemplate') as HTMLTextAreaElement;
                    if (textarea) {
                      const start = textarea.selectionStart;
                      const end = textarea.selectionEnd;
                      const text = textarea.value;
                      const before = text.substring(0, start);
                      const after = text.substring(end);
                      textarea.value = before + item.placeholder + after;
                      textarea.focus();
                      textarea.setSelectionRange(start + item.placeholder.length, start + item.placeholder.length);
                      setConfig({ ...config, messageTemplate: textarea.value });
                    }
                  }}
                >
                  {item.placeholder}
                  <span className="ml-1 text-xs text-gray-500">({item.description})</span>
                </Badge>
              ))}
            </div>
          </div>
        </CardContent>
      </Card>

      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <Phone className="h-5 w-5" />
            Phone Number Configuration
          </CardTitle>
          <CardDescription>
            Configure the phone numbers for sending and testing SMS messages.
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="space-y-2">
            <Label htmlFor="fromPhoneNumber">From Phone Number</Label>
            <Input
              id="fromPhoneNumber"
              type="tel"
              placeholder="+1234567890"
              value={config.fromPhoneNumber}
              onChange={(e) => setConfig({ ...config, fromPhoneNumber: e.target.value })}
            />
            <p className="text-xs text-gray-500">
              This is the Twilio phone number that will send the SMS messages.
            </p>
          </div>

          <div className="space-y-2">
            <Label htmlFor="testPhoneNumber">Test Phone Number (Optional)</Label>
            <Input
              id="testPhoneNumber"
              type="tel"
              placeholder="+1234567890"
              value={config.testPhoneNumber}
              onChange={(e) => setConfig({ ...config, testPhoneNumber: e.target.value })}
            />
            <p className="text-xs text-gray-500">
              Enter a phone number to test your SMS configuration.
            </p>
          </div>

          {config.testPhoneNumber && (
            <Button
              onClick={handleTestSMS}
              disabled={isTesting}
              variant="outline"
              className="w-full"
            >
              {isTesting ? 'Sending...' : 'Send Test SMS'}
            </Button>
          )}
        </CardContent>
      </Card>

      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <Info className="h-5 w-5" />
            How It Works
          </CardTitle>
        </CardHeader>
        <CardContent>
          <div className="space-y-3 text-sm text-gray-600">
            <div className="flex items-start gap-2">
              <div className="w-2 h-2 bg-blue-500 rounded-full mt-2 flex-shrink-0"></div>
              <p>When webhook data is received, the system will automatically extract phone numbers from the payload.</p>
            </div>
            <div className="flex items-start gap-2">
              <div className="w-2 h-2 bg-blue-500 rounded-full mt-2 flex-shrink-0"></div>
              <p>Your message template will be processed, replacing placeholders with actual data from the webhook.</p>
            </div>
            <div className="flex items-start gap-2">
              <div className="w-2 h-2 bg-blue-500 rounded-full mt-2 flex-shrink-0"></div>
              <p>An SMS will be sent to the extracted phone number using your configured Twilio account.</p>
            </div>
          </div>
        </CardContent>
      </Card>

      <div className="flex items-center gap-3 p-4 bg-yellow-50 border border-yellow-200 rounded-lg">
        <AlertCircle className="h-5 w-5 text-yellow-600 flex-shrink-0" />
        <div className="text-sm text-yellow-800">
          <p className="font-medium">Important Note</p>
          <p>Make sure your webhook data contains phone number fields (phone, mobile, cell, etc.) for this action to work properly.</p>
        </div>
      </div>

      <div className="flex justify-end gap-3 pt-4 border-t">
        <Button variant="outline" onClick={onCancel}>
          Cancel
        </Button>
        <Button onClick={handleSave} disabled={isLoading}>
          {isLoading ? 'Saving...' : 'Save Configuration'}
        </Button>
      </div>
    </div>
  );
};

export default TextWebhookConfig;
