import axiosInstance from "@/api/axios/axiosInstance";
import { toast } from "sonner";

// Types
export interface GmailProfile {
  email: string;
  messagesTotal: number;
  threadsTotal: number;
}

export interface GmailStatus {
  isConnected: boolean;
  needsReauth?: boolean;
  profile: GmailProfile | null;
}

export interface GmailEmail {
  id: string;
  messageId: string;
  fromAddress: string;
  email: string;
  name: string;
  subject: string;
  date: string;
  snippet: string;
  processed: boolean;
  createdAt: string;
}

export interface GmailEmailsResponse {
  emails: GmailEmail[];
  total: number;
  limit: number;
  offset: number;
}

// Generate Gmail OAuth URL
export async function generateGmailAuthUrl(): Promise<string> {
  try {
    const res = await axiosInstance.get("/api/gmail/auth/url");
    
    if (res.data.success) {
      return res.data.data.authUrl;
    }
    
    throw new Error(res.data.message || "Failed to generate Gmail auth URL");
  } catch (error: any) {
    console.error("Error generating Gmail auth URL:", error);
    const message = error.response?.data?.message || "Failed to generate Gmail auth URL";
    toast.error(message);
    throw error;
  }
}

// Handle Gmail OAuth callback
export async function handleGmailCallback(code: string, state: string) {
  try {
    const res = await axiosInstance.post("/api/gmail/auth/callback", {
      code,
      state
    });
    
    if (res.data.success) {
      toast.success("Gmail connected successfully!");
      return res.data.data;
    }
    
    throw new Error(res.data.message || "Failed to connect Gmail");
  } catch (error: any) {
    console.error("Error handling Gmail callback:", error);
    const message = error.response?.data?.message || "Failed to connect Gmail";
    toast.error(message);
    throw error;
  }
}

