// Connect Gmail (redirect to OAuth in same tab) - using backend API
export async function connectGmail(userId?: string, redirectPath?: string) {
  try {
    const response = await axiosInstance.get("/api/gmail/auth/url", {
      params: { redirectPath: redirectPath || '/webhooks' }
    });

    if (response.data.success) {
      window.location.href = response.data.data.authUrl;
    } else {
      throw new Error(response.data.message || "Failed to generate Gmail auth URL");
    }
  } catch (error: any) {
    console.error("Error generating Gmail auth URL:", error);
    const message = error.response?.data?.message || "Failed to generate Gmail auth URL";
    toast.error(message);
    throw error;
  }
}

// Connect Gmail for contact email functionality (opens in popup)
export async function connectGmailForContacts() {
  try {
    const response = await axiosInstance.get("/api/gmail/auth/url", {
      params: { redirectPath: '/contacts' }
    });

    if (response.data.success) {
      // Open Gmail OAuth in a popup window
      const popup = window.open(
        response.data.data.authUrl,
        'gmail-oauth',
        'width=500,height=600,scrollbars=yes,resizable=yes,left=' +
        (window.screen.width / 2 - 250) + ',top=' + (window.screen.height / 2 - 300)
      );

      if (!popup) {
        toast.error('Popup blocked. Please allow popups and try again.');
        return;
      }

      // Listen for popup completion
      return new Promise((resolve, reject) => {
        const checkClosed = setInterval(() => {
          if (popup.closed) {
            clearInterval(checkClosed);
            // Check if Gmail was connected successfully
            getGmailStatus()
              .then((status) => {
                if (status.isConnected) {
                  toast.success("Gmail connected successfully!");
                  resolve(status);
                } else {
                  reject(new Error("Gmail connection was cancelled"));
                }
              })
              .catch(reject);
          }
        }, 1000);

        // Timeout after 5 minutes
        setTimeout(() => {
          clearInterval(checkClosed);
          if (!popup.closed) {
            popup.close();
          }
          reject(new Error("Gmail connection timeout"));
        }, 300000);
      });
    } else {
      throw new Error(response.data.message || "Failed to generate Gmail auth URL");
    }
  } catch (error: any) {
    console.error("Error connecting Gmail for contacts:", error);
    const message = error.response?.data?.message || "Failed to connect Gmail";
    toast.error(message);
    throw error;
  }
}

