// Check if Gmail is configured
export function isGmailConfigured(): boolean {
  return !!(import.meta.env.VITE_GMAIL_CLIENT_ID);
}

// Send email via Gmail
export async function sendGmailEmail(emailData: {
  to: string;
  cc?: string;
  bcc?: string;
  subject: string;
  body: string;
  isHtml?: boolean;
  attachments?: string[];
}) {
  try {
    const res = await axiosInstance.post("/api/gmail/emails/send", emailData);

    if (res.data.success) {
      toast.success("Email sent successfully!");
      return res.data.data;
    }

    throw new Error(res.data.message || "Failed to send email");
  } catch (error: any) {
    console.error("Error sending Gmail email:", error);
    const message = error.response?.data?.message || "Failed to send email";
    toast.error(message);
    throw error;
  }
}

