// Star/unstar email
export async function starGmailEmail(messageId: string, star: boolean = true) {
  try {
    const res = await axiosInstance.post(`/api/gmail/emails/${messageId}/star`, { star });

    if (res.data.success) {
      toast.success(`Email ${star ? 'starred' : 'unstarred'} successfully!`);
      return res.data.data;
    }

    throw new Error(res.data.message || `Failed to ${star ? 'star' : 'unstar'} email`);
  } catch (error: any) {
    console.error(`Error ${star ? 'starring' : 'unstarring'} Gmail email:`, error);
    const message = error.response?.data?.message || `Failed to ${star ? 'star' : 'unstar'} email`;
    toast.error(message);
    throw error;
  }
}

