// Send bulk emails via Gmail
export async function sendBulkGmailEmails(emails: Array<{
  to: string;
  subject: string;
  body: string;
  isHtml?: boolean;
}>) {
  try {
    const res = await axiosInstance.post("/api/gmail/emails/send-bulk", { emails });

    if (res.data.success) {
      const successCount = res.data.data.filter((r: any) => r.success).length;
      toast.success(`${successCount} emails sent successfully!`);
      return res.data.data;
    }

    throw new Error(res.data.message || "Failed to send bulk emails");
  } catch (error: any) {
    console.error("Error sending bulk Gmail emails:", error);
    const message = error.response?.data?.message || "Failed to send bulk emails";
    toast.error(message);
    throw error;
  }
}
