  const handleGmailOAuthSuccess = async (data: any) => {
    console.log('üéâ Gmail OAuth success in WebhookCreator:', data);
    setGmailAuthStatus('connected');

    // Re-check Gmail status from backend to ensure consistency
    try {
      const response = await axiosInstance.get('/api/gmail/auth/status');
      if (response.data.success && response.data.data.isConnected) {
        console.log('‚úÖ Gmail status confirmed from backend:', response.data.data);
        // If a specific sidebar card is selected, mark only that card as connected
        if (selectedCardForSidebar) {
          setActionCards(prevCards => prevCards.map(card =>
            card.id === selectedCardForSidebar && card.selectedApp?.id === 'gmail-service'
              ? { ...card, isConnected: true }
              : card
          ));
        }

        // Trigger auto-save if in edit mode to persist the connection status
        if (isEditMode) {
          triggerAutoSave();
        }
      }
    } catch (error) {
      console.error('Error re-checking Gmail status:', error);
    }

    console.log('‚úÖ Gmail connection status updated successfully');
  };

  // Ensure a draft webhook exists in DB so autosave can update it
  const [currentWebhookId, setCurrentWebhookId] = useState<string | number | null>(null);
  const ensureWebhookRecord = async () => {
    try {
      console.log('üîß ensureWebhookRecord called with:', {
        webhookCreated,
        currentWebhookId,
        workflowTitle,
        selectedTrigger: selectedTrigger?.id,
        selectedApp: selectedApp?.id,
        webhookUrl
      });

      if (webhookCreated || currentWebhookId) {
        console.log('üîß Webhook already exists, skipping creation');
        return;
      }
      if (!workflowTitle || !workflowTitle.trim()) {
        console.log('üîß No workflow title, skipping webhook creation');
        return;
      }
      if (!selectedTrigger?.id) {
        console.log('üîß No trigger selected, skipping webhook creation');
        return;
      }

      // For Facebook Lead Ads, allow creating webhook record without URL initially
      const isFacebookLeadAds = selectedApp?.id === 'facebook-lead-ads';
      if (!webhookUrl && !isFacebookLeadAds) {
        console.log('üîß No webhook URL and not Facebook Lead Ads, skipping webhook creation');
        return;
      }

      const payload = {
        name: workflowTitle,
        trigger_event: selectedTrigger.id,
        trigger_app: selectedApp?.id || 'webhook',
        description: selectedTrigger.description,
        url: webhookUrl || '', // Allow empty URL for Facebook Lead Ads initially
        is_active: false,
      };

      console.log('üîß Creating webhook with payload:', payload);
      const response = await axiosInstance.post('/api/webhooks', payload);
      console.log('üîß Webhook creation response:', response.data);

      if (response.data?.success) {
        const createdId = response.data.data?.id || response.data.data?.webhook_id;
        if (createdId) {
          console.log('‚úÖ Webhook created successfully with ID:', createdId);
          setCurrentWebhookId(createdId);
          setWebhookCreated(true);
          setIsEditMode(true);
          setLastSaved(new Date());
          console.log('‚úÖ Draft webhook created for autosave:', createdId);

          // Transfer temporary Facebook connection to permanent storage
          if (selectedApp?.id === 'facebook-lead-ads') {
            const tempConnected = sessionStorage.getItem('temp_facebook_connected');
            const tempUser = sessionStorage.getItem('temp_facebook_user');

            if (tempConnected === 'true' && tempUser) {
              localStorage.setItem(`facebook_connected_${createdId}`, 'true');
              localStorage.setItem(`facebook_user_${createdId}`, tempUser);

              // Keep temporary storage for now - it will be cleared when webhook is finalized
              // sessionStorage.removeItem('temp_facebook_connected');
              // sessionStorage.removeItem('temp_facebook_user');

              console.log('‚úÖ Transferred temporary Facebook connection to webhook:', createdId);
            }
          }
        }
      }
    } catch (error) {
      console.error('Failed to ensure webhook record (draft):', error);
    }
  };

